@media screen and (min-width: 569px) {
 .bannerTitle {
 margin-top: 4rem; }
 .vCard {
 @include size(20rem); } }

@media screen and (min-width: 321px) and (max-width: 568px) and (orientation: landscape) {
 .sectionHero {
 margin: 1.4rem auto 0; } }

