/* Tipography
 ** Table of contens:
 *
 * ––––––––––––––––––––––––––––––––––––––––––––––––– */

h1, h2, h3, h4, h5, h6 {
 margin-top: 0;
 margin-bottom: 3rem;
 @include font(monospace, false, false, 600, false);
 word-break: keep-all;
 hyphens: auto; }

h1 {
 @include font(false, 3.0rem, false, false, 1.2);
 letter-spacing: .4rem; }

h2 {
 @include font(false, 2.6rem, false, false, 1.38);
 } //letter-spacing: -.1rem

/*
 *- NOTE: scala aurea
 *
 * ––––––––––––––––––––––––––––––––––––––––––––––––– */


@include mediaQ(exsmall) {
 h1 {
 @include font(false, 5.0rem, false, false, 1.1); }
 h2 {
 @include font(false, 4.2rem, false, false, 1.18); } }

p {
 /* from main: +font($systemFF,1.6rem,$text,400,1.6) */
 @include font(false, false, false, false, false);
 margin-top: 0;
 hyphens: auto; }

@include mediaQ(exsmall) {
 p {
 @include font(false, 1.8rem, false, false, 1.8); } }

/* Tipography
 ** Table of contens:
